import React from 'react';
import PropTypes from 'prop-types';
import { Switch, Route, Link, withRouter } from 'react-router-dom'

import { CSSTransition, TransitionGroup } from 'react-transition-group';

import Header from './components/Header';
import Body from './components/Body';
import Footer from './components/Footer';
import Section from './components/Section';
import ColorPallete from './components/ColorPallete';
import Navigation from './components/Navigation';
import palette from './palette';
import ExperienceList from './components/ExperienceList';
import PersonalProjects from './components/PersonalProjects'
import EducationList from './components/EducationList';
import Skills from './components/Skills';

class PageFade extends React.Component {
    componentWillEnter (callback) {
    }    
    componentWillLeave (callback) {
    }
        
    render(){
        return (    <CSSTransition 
            {...this.props}
            classNames="fadeTranslate"
            timeout={400}
            mountOnEnter={true}
            unmountOnExit={true}
        />
    )
    }

}


const Navigaton = ({theme})=>(<nav className={theme.nav} >
        <Link className={theme.navLinks} to='/'>Home</Link>
        <Link className={theme.navLinks} to='/body'>Roster</Link>
    </nav>)

class NavigationMenu extends React.PureComponent {
    constructor(props){
        super(props);
        const x= Array(5).fill(false);
        x[0] = true;
        this.state = {
            hover:x
        }
    }
    hovered(ind,isIn){
        this.setState(() => {
            const newHover = Array(5).fill(false); 
            newHover[ind]=isIn;
                return {
                hover:newHover
            }
        })
    }
    render(){
        return (<nav className={this.props.theme.navi}>
        {Array(5).fill(0).map((val,ind)=><div  onMouseEnter={(evt)=>{this.hovered(ind,true)}}  className={
            this.state.hover[ind] ?  this.props.theme.naviItem + ' ' + this.props.theme.activeNaviItem : this.props.theme.naviItem
            }>{
                Array(3).fill(0).map(val=><a className={this.props.theme.innerNaviLink}></a>)
            }</div>)}
</nav>)
    }
}

class Appa extends React.PureComponent {
    constructor(props){
        super(props);
        this.state = {
            paletteIndex:0,
            navigationState:0,
            maxNavigationStates:2,
        };
    }
    render() {
        const {
            name,
            firstName,
            lastName,
            tagline,
            introduction,
            labels,
            experience,
            education,
            personalProjects,
            navigation,
            avatarUrl,
            skills,
        } = { ...this.props.data };
        const theme = {...this.props.theme};
        const locationKey = {...this.props.location.pathname};
        return (
            <div className={theme.App}>
                <ColorPallete {...palette[this.state.paletteIndex]}/>
               {/* <Navigation theme={theme}/>*/}
                <Link to='/body'>BODY</Link>                <Link to='/'>HOME</Link>
                <TransitionGroup>
                <PageFade key={locationKey}>

    <Switch>
        <Route exact path='/' component={()=><Section className={theme.leftSection}>
            <Header
                  name={name}
                  tagline={tagline}
                  introduction={introduction}
                  labels={labels}
                  className={theme.header}
                  theme={theme}
                  firstName={firstName}
                  lastName={lastName}
              />
            </Section>}/>
        <Route exact path='/body' component={()=>( <Section className={theme.rightSection}>
                                    <Body
                                        experienceListData={experience}
                                        educationListData={education}
                                        personalProjects={personalProjects}
                                        className={theme.body}
                                        theme={theme}
                                        skills={skills}
                                    />
                                    <Footer theme={theme} />
        </Section>)}/>
        </Switch>

        </PageFade>
            </TransitionGroup>

            </div>
        );
    }
}


const About = () => (
    <p>About</p>
);

const Home = () => (
    <p>Home</p>
);


const NotFound = () => (
    <p>404</p>
);


class  App extends React.PureComponent {  
    menuClick(){
        document.querySelector('.menu-toggle').checked = false;
    }
    activateLink(path){
        const alist = document.querySelectorAll('a');
        if(alist)
          alist.forEach((val)=>{
            val.href.endsWith( path) ? val.classList.add('activated') : val.classList.remove('activated');
        });
    }
    componentDidMount(){
        this.activateLink(this.props.location.pathname);
    }
    componentDidUpdate(prevProps){
            this.activateLink(this.props.location.pathname);
    }
    render (){
        const props = this.props;
    const locationKey = props.location.pathname;
    const theme = props.theme;
    const {experience,education,personalProjects,skills} = {...props.data};

    return (
        <div className={theme.App}>
                    <style>
                {`.fadeTranslate-enter {
  opacity: 0;
}

.fadeTranslate-enter.fadeTranslate-enter-active {
  opacity: 1;
  transition: opacity 1000ms ease-in;
}

.fadeTranslate-exit {
  opacity: 1;
  transform: translate(0, 0);
}

.fadeTranslate-exit.fadeTranslate-exit-active {
  opacity: 0;
  transform: translate(0, 300vh);
  transition: opacity 1000ms ease-in, transform 1000ms ease-in-out;
}

.fix-container {
  position: fixed;
}
.link-nav {
    width: 20vw;
    float:left;
}
.display-section {
}

nav {
    position:fixed;
    z-index:9999;
  }
  .menu-toggle {
    position:relative;
    opacity:1;
    width:20px;
    height:30px;
    z-index: 9999;
    opacity:0;
    margin:0;
  }
  .menu-toggle ~ div {
    overflow:hidden;
    height:0;
    width:18px;
    border-bottom:2px solid white;
    transition: width 500ms ease,height 1000ms cubic-bezier(1,.01,.26,-0.06);
    position:absolute;
    top: 25px;
  }
  .menu-toggle:checked ~ div {
    width:100vw;
    height:100vh;
    
  }
  .menu-toggle:checked ~ .cross {
     background: blue;
  }
  
  .cross:before {
    content:" ";
    position:absolute;
    left:0px;
    top:5px;
    width:18px;
    height:2px;
    background:white;
    transition: transform 1000ms, width 500ms;
    transform: rotate(0deg);
    transform-origin: left top;
  }
  .cross:after {
    content:" ";
    position:absolute;
    left:0px;
    top:15px;
    width:18px;
    height:2px;
    background:white;
    transition: transform 1000ms, width 500ms;
    transform: rotate(0deg);
    transform-origin: left top;
    
  }
  
  .menu-toggle:checked ~ .cross:before{
    transform: rotate(30deg);
    width:20px;
    transform-origin: left top;
  }
  .menu-toggle:checked ~ .cross:after{
    transform: rotate(-30deg);
    width:20px;
    transform-origin: left top;
  }
  .menu-page {
      background:rgba(0,0,0,0.8);
  }
  .menu-list {
  }
  .menu-list li {
      list-style:none;
      list-style: none;
      width: 16.66%;
      height:15vh;
      color:white;
      background: #000;
      width:100%;
       }
  .menu-list li a {
      color:white;
      text-decoration:none;
      font-size:400%;
      width:100%;
      height:100%;
      display: flex;
      justify-content: center;
      align-items: center;
      letter-spacing: 0;
      transition: letter-spacing 1000ms;
  }
  .menu-list li a:hover {
    letter-spacing: 10px;
  }
  
  .menu-list li a:before {
    content: " ";
    position: relative;
    top: 0;
    width: 0;
    height:3px;
    background:white;
    transition: width 1s;
  }
  .menu-list li a:hover:before {
    width: 30%;
  }
  @keyframes shadowCover {
      0% {
        background: linear-gradient(90deg,#222 0%, black);
      }
      100%{
        background: linear-gradient(90deg,#222 100%, black);
      }
  }
  .menu-list li a:after {
    content: " ";
    position: absolute;
    /* border: 2px solid white; */
    left: 0;
    height: 100px;
    width: 0;
    background: linear-gradient(90deg, rgba(0,0,0,0.1), rgba(200,200,200,0.2));
    transition: width 300ms;
    
  }
  .menu-list li a:hover:after {
    width: 100%;
    
  }
  
 .menu-list li a:hover{
 }

a.activated {
      background:#222
    }

`}
            </style>
            <section>
    
            <nav>
            <input type="checkbox" className="menu-toggle" id="toggle-1"/>
            <span className="cross"></span>
            <div className="menu-page"><div>            <ul className="menu-list">
                <li><Link onClick={this.menuClick} className="menu-links" to="/">Home</Link></li>
                <li><Link onClick={this.menuClick} className="menu-links" to="/about">About Me</Link></li>
                <li><Link onClick={this.menuClick} className="menu-links" to="/work/0">Work Experience</Link></li>
                <li><Link onClick={this.menuClick} className="menu-links" to="/personal">Personal Projects</Link></li>
                <li><Link onClick={this.menuClick} className="menu-links" to="/skills">Skills</Link></li>
                <li><Link onClick={this.menuClick} className="menu-links" to="/education">Education</Link></li>
                </ul>
</div></div>
          </nav>

                    <section className="display-section">

                        <Switch location={props.location}>
                        <Route exact path="/" component={()=><Section className={theme.leftSection}>
                        <Header
                              className={theme.header}
                              theme={theme}
                          />
                        </Section>} />
                        <Route path="/work" component={()=> (<div><ExperienceList experienceListData={experience} theme={theme}/></div>)} />
                        <Route exact path="/personal" component={()=> (<div><PersonalProjects projectList={personalProjects} theme={theme}/></div>)} />
                        <Route exact path="/skills" component={()=> (<div><Skills theme={theme} skillsPieChart={skills.skillsPieChart} /></div>)} />
                        <Route exact path="/education" component={()=> (<div><EducationList educationListData={education} theme={theme} /></div>)} />
                        
                        
                                        <Route exact path="/about" component={()=>( <Section className={theme.rightSection}>
                                    <Body
                                        experienceListData={experience}
                                        educationListData={education}
                                        personalProjects={personalProjects}
                                        className={theme.body}
                                        theme={theme}
                                        skills={skills}
                                    />
                                    <Footer theme={theme} />
        </Section>)} />

                            <Route component={NotFound} />
                        </Switch>
                    </section>
            </section>

</div>    );
}
}

export default withRouter(App);



