import React from 'react';
import { Switch, Route, Link, withRouter } from 'react-router-dom'

class Navigation extends React.PureComponent {
    constructor(props){
        super(props);
        this.state = {
            active:false
        }
    }
    changeState(){
        console.log('change state')
        const active = !this.state.active;
        this.setState({active});
    }
    render(){
        const {navigationData, theme,avatarUrl } = {...this.props};
        console.log(this.state.active)
        return (
            <nav className={ !this.state.active ? theme.customNavigation : theme.customNavigation + ' ' + theme.active} onClick={(evt)=>{console.log('clickEvt');this.changeState()}}>
                <div className={theme.navImage}>
                    <img src={avatarUrl}/>
                </div>
                <div className={theme.navContent}>
                <Link className={theme.navLinks} to='/'>Home</Link>
            <Link className={theme.navLinks} to='/body'>Roster</Link>
            <Link className={theme.navLinks} to='/'>Home</Link>
            <Link className={theme.navLinks} to='/body'>Roster</Link>
            <Link className={theme.navLinks} to='/'>Home</Link>
            <Link className={theme.navLinks} to='/body'>Roster</Link>
                </div>
            </nav>     
        );     
    }
}

export default Navigation;



