import React from 'react';
import { Switch, Route, Link, withRouter } from 'react-router-dom'

import Experience from './Experience';

const ExperienceList = ({experienceListData, className, theme}) => {
    return (
        <div className={theme.experienceList}>
            <h3 className={theme.experienceLabel} id="experience">EXPERIENCE</h3>
            <div className={theme.experienceSection}>
           <div className={theme.experienceLinks}> {
                experienceListData.map((val,ind)=><Link to={`/work/${ind}`}>
                {val.organization}
                </Link>)
            }
</div>
            <Route exact path="/work/:org" component={(props)=> (<Experience experienceListData={experienceListData} {...props} theme={theme} />)} />
            </div>
        </div>       
    );            
};

export default (ExperienceList);



